import {
  Component,
  OnInit
} from '@angular/core';
import {
  ApiconnectService
} from '../../services/apiconnect.service';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {

  constructor(private _api: ApiconnectService) {}

  datos: any = {
    "coord": {
      "lon": -72.7167,
      "lat": -37.8
    },
    "weather": [{
      "id": 804,
      "main": "Clouds",
      "description": "overcast clouds",
      "icon": "04n"
    }],
    "base": "stations",
    "main": {
      "temp": 289.53,
      "feels_like": 288.62,
      "temp_min": 289.53,
      "temp_max": 289.53,
      "pressure": 1015,
      "humidity": 71,
      "sea_level": 1015,
      "grnd_level": 1005
    },
    "visibility": 10000,
    "wind": {
      "speed": 1.8,
      "deg": 212
    },
    "clouds": {
      "all": 97
    },
    "dt": 1613105855,
    "sys": {
      "country": "CL",
      "sunrise": 1613125060,
      "sunset": 1613173963
    },
    "timezone": -10800,
    "id": 3899629,
    "name": "Angol",
    "cod": 200
  }
  flag: string = `https://flagcdn.com/${this.datos.sys.country.toLowerCase()}.svg`;
  sky: string = `${this.datos.weather[0].icon}.png`;
  temp: number= Math.floor(this.datos.main.temp - 273);

  async ngOnInit() {
    this._api.data$.subscribe(data => this.change(this.datos = data))
  }

  change(data) {
    this.datos = data;
    this.flag = `https://flagcdn.com/${data.sys.country.toLowerCase()}.svg`;
    this.sky = `${data.weather[0].icon}.png`
    this.temp = Math.floor(data.main.temp - 273);
  }





}
