import { Component, OnInit} from '@angular/core';
import Citys from 'src/assets/json/city.json';
import Countrys from 'src/assets/json/country.json';
import {ApiconnectService} from '../../services/apiconnect.service';

@Component({
  selector: 'app-searcher',
  templateUrl: './searcher.component.html',
  styleUrls: ['./searcher.component.scss']
})
export class SearcherComponent implements OnInit {


  constructor(private _api:ApiconnectService){

  }

  //variables para usar en HTML
  country:any=Countrys; //todos los paises con su nombre y su abreviacion
  countryback:any=[];
  cityView:any = [];  //array de todas las ciudades de un pais seleccionado
  cityback:any[];
  pais:string;
  pass2:boolean= false;
  //variables globarles para hacer la logica
  countryArrayV:any = []; // array de los nombres de los paises
  citys:any=Citys;   //Todas las ciudades con la informacion necesaria para consumir la api
  passApi:boolean = false;
  code:string

  //Selecciona a chile para que los inputs no esten vacios al comienzo
  ngOnInit(){
    Countrys.forEach(c=>{
      this.countryArrayV.push(Object.values(c).toString())
    });

    this.countryback=this.country;
  }

  valor2(f,g,c,li){
    const change:any = this.valor(c,g);
    if(change){
      f.value= c.value
    }

  }

  //cambia el pais seleccionado
  valor(f,g,...change){

    const reg = new RegExp(`^${f.value.toLowerCase().trim()}`);
    this.country = this.countryback;
    this.country = this.country.filter(word => reg.test(Object.values(word).toString().toLowerCase().trim()));
    let pais;

    g.value = '' //reinicia el valor de las ciudades en el html #aspecto visual
    let code:any; //codigo del pais #variable local
    let pass:boolean = false;
    let none:boolean = false;
    this.countryArrayV.forEach(function(v){
      if(v.toString().toLowerCase().trim() == f.value.toLowerCase().trim()){
        pais= f.value;
        code = Countrys.filter(cod=> (Object.values(cod).toString().toLowerCase().trim() == f.value.toLowerCase().trim()))
        pass = true; //se verifica si el pais escrito existe
      }
      else{
        none=true
      }
    });

    if(none){
      this.cityView = [];
      this.pais='';
    }

    this.pass2=false;
    if(pass){
      this.code = code
      this.pass2 = true;
      this.cityView = [];
      this.pais = pais
      //se agregaran todas las ciudades de ese pais por un filtro el cual es si coincide con el codigo del pais seleccionado
      Citys.forEach(co=>{
        if(co.country.toUpperCase().trim() == Object.keys(code[0]).toString().toUpperCase().trim()){
          this.cityView.push(co.name);
        }
      })
      this.cityView.sort();
      this.cityback = this.cityView;
    }
    return pass
  }

  value(c){
    return Object.values(c);
  }


  cityCheck(g,b){

    if(this.pass2){
      this.cityView = this.cityback;
      const reg = new RegExp(`^${g.value.toLowerCase().trim()}`);
      this.cityView = this.cityView.filter(word => reg.test(word.toLowerCase().trim()))
      let pass:boolean = false;
      this.passApi = false;
      this.cityView.forEach(element=>{
        if(element.toLowerCase().trim() == g.value.toLowerCase().trim()){
          this.passApi = true;
        }
      })
    }

  }

  focusout(li){
    li.style.opacity = '0';
    li.style.visibility='hidden';

  }
  focus(li,g){
    g.value=""
    li.style.opacity = '1';
    li.style.visibility = 'visible'
  }

  focusout2(li){
    li.style.opacity = '0';
    li.style.visibility='hidden';

  }
  focusTwo(li){
    li.style.opacity = '1';
    li.style.visibility = 'visible'
  }
  changecity(cy,g,button){
    g.value=cy.value
    this.cityCheck(g,button)
  }
  apiConnect(cityid){

    if(this.passApi){
      const obid = Citys.filter(c=> c.name.toLowerCase().trim() == cityid.value.toLowerCase().trim() && c.country == Object.keys(this.code[0]).toString() )
      const id = obid[0].id.toString();
      this._api.request(id).subscribe(datas => this._api.data$.emit(datas));
    }
  }


  change(items){

  }

}
