import { Injectable,EventEmitter } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs/internal/Observable';
import {Api} from '../interfaces/interfaceApiWeather';

@Injectable({
  providedIn: 'root'
})
export class ApiconnectService {

  data$ = new EventEmitter<object>();

  constructor(private http:HttpClient) { }

  apiKey:string = '70eed00973c0442d27911a654bbcd325';

   request(idcity):Observable<Api[]>{

    return this.http.get<Api[]>(`http://api.openweathermap.org/data/2.5/weather?id=${idcity}&appid=${this.apiKey}`)
  }



}
